import type { Placement } from '@floating-ui/react';
import { css, excludeProps, styled, variants } from '@lokalise/styled';
import type { HTMLAttributes, RefAttributes } from 'react';

import { typography, value } from '../../utils/theme';

const bottom = css`
	top: -5px;

	&::before {
		border-left-width: 1px;
		border-left-style: solid;
		border-top-width: 1px;
		border-top-style: solid;
	}
`;
const top = css`
	bottom: -5px;

	&::before {
		border-right-width: 1px;
		border-right-style: solid;
		border-bottom-width: 1px;
		border-bottom-style: solid;
	}
`;
const left = css`
	right: -5px;

	&::before {
		border-right-width: 1px;
		border-right-style: solid;
		border-top-width: 1px;
		border-top-style: solid;
	}
`;
const right = css`
	left: -5px;

	&::before {
		border-left-width: 1px;
		border-left-style: solid;
		border-bottom-width: 1px;
		border-bottom-style: solid;
	}
`;

export type ArrowProps = {
	placement?: Placement;
} & HTMLAttributes<HTMLElement> &
	RefAttributes<HTMLElement>;

export const Arrow = styled.div.withConfig({ shouldForwardProp: excludeProps(['placement']) })<ArrowProps>`
	position: absolute;
	width: 8px;
	height: 8px;
	z-index: ${value('zIndex.hide')};

	&::before {
		position: absolute;
		width: 8px;
		height: 8px;
		z-index: ${value('zIndex.hide')};
		content: '';
		display: block;
		transform: rotate(45deg);
		background-color: ${value('color.background.surface.default')};
		border-color: ${value('color.border.default')};
	}

	${variants(
		'placement',
		{
			top,
			bottom,
			left,
			right,
			'right-end': right,
			'right-start': right,
			'left-start': left,
			'auto-end': css``,
			'auto-start': css``,
			auto: css``,
			'bottom-end': bottom,
			'bottom-start': bottom,
			'left-end': left,
			'top-end': top,
			'top-start': top,
		},
		'auto',
	)}
`;

export const Content = styled.div<HTMLAttributes<HTMLElement>>`
	padding: 0;
	border-radius: ${value('radius.tooltip')};
	background-color: ${value('color.background.surface.default')};
	border: 1px solid ${value('color.border.default')};
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
	z-index: ${value('zIndex.popover')};
	color: ${value('color.text.default')};
	${typography('body.small.default')};
	width: max-content;
`;
